import React, { useContext, useEffect, useState } from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'
import ContentLoader from "react-content-loader";
import classNames from 'classnames'
import { useTranslation } from 'react-i18next'
import { BigNumber } from 'bignumber.js';
import truncateMiddle from 'truncate-middle'
import { NetworkTypeContext, WalletAddressContext, Web3Context } from '../../context'
import FetchData from '../../methods/FetchData'
import CoreData from '../../methods/CoreData'
import {loadingColors} from '../../utils/color'
import { promiseWithTimeout } from '../../utils/promise'
import Config from '../../utils/config';
import LiquidateModal from '../../components/subComponents/LiquidateModalV2/LiquidateModal'
import { BgGradient, PaginationComponent, TwinLogo, TorTooltip } from '../../components/littleComponents'
import { cloneDeep } from 'lodash-es';
import { MdExpandMore } from "@react-icons/all-files/md/MdExpandMore"
import { MdExpandLess } from "@react-icons/all-files/md/MdExpandLess"
import { formatDecimalPlace } from '../../utils/numberFormat'
import { Multicall } from 'ethereum-multicall'
import styles from './Liquidate.module.scss'


const pageSize = 10 // 每页显示10条
const liquidatePercent = 95 // 显示清算列表的百分比
const serverePercent = 100 // >= 100 可清算
const warningPercent = 95 // >= 95 && < 100


async function loadLiquidityData(networkType) {
  try {
    const data = await fetch(`${Config.liquidateService.host}/${networkType.toLowerCase()}?health=${liquidatePercent * 100}`) // ?health=5000
    return await data.json()
  } catch(e) {
    return []
  }
}

async function loadAllMarketData(web3, networkType, connectedAddress, marketsArr, mainNetWeb3) {
  await FetchData.callContract(web3, connectedAddress, networkType, mainNetWeb3)
  FetchData.clearMarketDataCache()
  await FetchData.cacheAllMarketData(web3, networkType, connectedAddress, marketsArr)
  const promises = []
  for (let market of marketsArr) {
    promises.push(loadMarketData(web3, networkType, connectedAddress, market))
  }

  const dataTree = await Promise.all(promises)
  const promiseArr = [
    FetchData.getUSDTInUSD(web3, networkType).then(response => {
      dataTree.usdtInUSD = response
    })
  ]

  await Promise.all(promiseArr);

  return dataTree
}

async function loadMarketData(web3, networkType, connectedAddress, market) {
  await Promise.all([
    FetchData.getWalletBalance(web3, connectedAddress, networkType, market)
      .then(response => {
        market.walletBalance = response.walletBalance
        market.walletBalanceFormatted = response.walletBalanceFormatted
        market.walletBalanceFiat = response.walletBalanceFiat
      }),
    FetchData.getSavingsBalance(web3, connectedAddress, networkType, market)
      .then(response => {
        market.savingsBalance = response.savingsBalance
        market.savingsBalanceFormatted = response.savingsBalanceFormatted
        market.savingsBalanceFiat = response.savingsBalanceFiat
        market.savingsCTokenBalance = response.savingsCTokenBalance
      }),
    FetchData.getPrice(web3, networkType, market).then(response => {
      market.price = response
    }),
    FetchData.getLiquidateAllowance(web3, connectedAddress, networkType, market).then(response => {
      if (CoreData.isNativeToken(market.symbol, networkType)) {
        market.liquidateApproved = true
      } else {
        market.liquidateApproved = response.allowance !== 0
      }
    })
  ])
  return market
} 

function Liquidate(props) {
  const { connectedAddress } = useContext(WalletAddressContext)
  const { networkType } = useContext(NetworkTypeContext)
  const { web3, readonlyWeb3, hecoMainNetWeb3 } = useContext(Web3Context)
  const { t, i18n } = useTranslation()
  const [loading, setLoading] = useState(true)
  const [showLoadAlertModal, setShowLoadAlertModal] = useState(false)
  const [originTableData, setOriginTableData] = useState([]) // 初始数据
  const [tableData, setTableData] = useState([])  // 排序后的全部数据
  const [curTableData, setCurTableData] = useState([]) // 排序后，分页的数据
  const [activeData, setActiveData] = useState() // 显示弹窗的数据
  const [activeTableIndex, setActiveTableIndex] = useState() // 显示弹窗的表格索引
  const [showLiquidateModal, setShowLiquidateModal] = useState(false)
  const [currentPage, setCurrentPage] = useState(1)
  const [totalPages, setTotalPages] = useState(0)
  const [sortField, setSortField] = useState('')
  const [sortDirection, setSortDirection] = useState(true)
  const [expand, setExpand] = useState({}) // {tableIndex: true}
  const [assetSelected, setAssetSelected] = useState({}) // {tableIndex: {borrow: assetIndex, supply: assetIndex}}
  const marketsArr = FetchData.getNetworkMarkets(networkType)

  const getMultiData = async(data) => {
    let results = {}
    let contractCallContext = []
    const multicall = new Multicall({
        multicallCustomContractAddress: Config.multiCall.network[networkType].address,
        web3Instance: web3
    })

    const multiNum = 7

    for (let [index, item] of data.entries()) {
      // if ( index <= multiNum * 2 ) {
        const {account:connectedAddress} = item
        
        // 抵押状态
        contractCallContext.push({
          reference: connectedAddress + ".getAssetsIn",
          contractAddress: Config.comptroller.network[networkType].address,
          abi: Config.comptroller.ABI,
          calls: [{ reference: 'comptroller.getAssetsIn.' + connectedAddress, methodName: "getAssetsIn", methodParameters: [connectedAddress] }]
        })
  
        for (let market of marketsArr) {
          // 存款、借款
          contractCallContext.push({
            reference: connectedAddress + '.' + market.symbol + ".fToken",
            contractAddress: market.qToken.network[networkType].address,
            abi: market.qToken.ABI,
            calls: [
              { reference: market.symbol + '.fToken.borrowBalanceCurrent.' + connectedAddress, methodName: 'borrowBalanceCurrent', methodParameters: [connectedAddress] },
              { reference: market.symbol + '.fToken.balanceOfUnderlying.' + connectedAddress, methodName: 'balanceOfUnderlying', methodParameters: [connectedAddress] }, 
              { reference: market.symbol + '.fToken.balanceOf.' + connectedAddress, methodName: 'balanceOf', methodParameters: [connectedAddress] }]
          })
      
          if (!CoreData.isNativeToken(market.symbol, networkType)) {
            contractCallContext.push({
              reference: connectedAddress + '.' + market.symbol + '.decimals',
              contractAddress: market.network[networkType].address,
              abi: market.ABI,
              calls: [{ 
                reference: market.symbol + '.decimals', 
                methodName: 'decimals'
              }]
            })
          }
      
          // 借款限制
          contractCallContext.push({ 
            reference: connectedAddress + '.' + market.symbol + ".compoundLens",
            contractAddress: Config.compoundLens.network[networkType].address,
            abi: Config.compoundLens.ABI,
            calls: [{ reference: market.symbol + '.compoundLens.cTokenMetadataExpand', methodName: 'cTokenMetadataExpand', methodParameters: [market.qToken.network[networkType].address] }]
          })
  
        }
        
        // 测试：multi超过7个帐户，数据太多获取不到
        if ( (index + 1) === data.length || (index+1) % multiNum === 0 ) {
          let temp = (await multicall.call(contractCallContext)).results;
          contractCallContext = []
          results = {...results, ...temp}
        } 
      // }
    }      
    return results
  }

  const getCateDataFromMulti = (multiData) => {
    const results = {}
    for (let key in multiData) {
      const item = multiData[key]
      const callsReturnContext = item.callsReturnContext
      const keyArr = key.split('.')
      const connectedAddress = keyArr[0]
      if (!results[connectedAddress]) {
        results[connectedAddress] = {}
      }
      if (keyArr[1] === 'getAssetsIn') {
        results[connectedAddress]['getAssetsIn'] = callsReturnContext[0].returnValues
      } else {
        const symbol = keyArr[1]
        const reference = keyArr[2]
        if (!results[connectedAddress][symbol]) {
          results[connectedAddress][symbol] = {}
        }
        switch(reference) {
          case 'fToken':
            callsReturnContext.forEach(i => {
              const {methodName, returnValues} = i
              results[connectedAddress][symbol][methodName] = new BigNumber(returnValues[0].hex).toString(10)
            })
            break;
          case 'decimals':
            results[connectedAddress][symbol]['decimals'] = callsReturnContext[0].returnValues[0]
            break;
          case 'compoundLens':
            results[connectedAddress][symbol]['compoundLens'] = new BigNumber(callsReturnContext[0].returnValues[0].hex).toString(10)
            break;
        }
      }
    }
    return results
  }

  const getAssetData = async(data) => {
    let results = {}
    for (let account in data) {
      results[account] = {}
      let totalLoanBalance = 0
      let totalBorrowLimitFiat = 0
      const item = data[account]
      const  {getAssetsIn, ...assetData} = item
      
      for (let symbol in assetData) {
        const assetItem = assetData[symbol]
        const market = Config.markets[symbol]
        const price = await FetchData.getPrice(web3, networkType, market)
  
        // 借款 、存款 
        const loanBalance = assetItem['borrowBalanceCurrent']
        const savingsBalance = assetItem['balanceOfUnderlying']
        const savingsCTokenBalance = assetItem['balanceOf']
        let loanBalanceFormatted, loanBalanceFiat, savingsBalanceFormatted, savingsBalanceFiat
  
        if (CoreData.isNativeToken(symbol, networkType)) {
          loanBalanceFormatted = CoreData.fromWei(web3, loanBalance, 'ether')
          loanBalanceFiat = loanBalanceFormatted * price
    
          savingsBalanceFormatted = CoreData.fromWei(web3, savingsBalance, 'ether')
          savingsBalanceFiat = savingsBalanceFormatted * price
        } else {
          const decimals = await FetchData.getDecimals(web3, networkType, market);
          loanBalanceFormatted = (loanBalance / Math.pow(10, parseInt(decimals)))
          loanBalanceFiat = loanBalanceFormatted * price
    
          savingsBalanceFormatted = (savingsBalance / Math.pow(10, parseInt(decimals)))
          savingsBalanceFiat = savingsBalanceFormatted * price
        }
  
        // 抵押状态
        let collateralStatus = false
        const theNetwork = market.qToken.network[networkType]
        if (!theNetwork) {
          collateralStatus = false
        } else {
          collateralStatus = getAssetsIn.findIndex(collateral => {
            return collateral.toLowerCase() === theNetwork.address.toLowerCase()
          }) >= 0
        }
  
        // 借款限制
        const collateralFactorMantissa = assetItem['compoundLens']
        const collateralFactor = collateralFactorMantissa / 1e18
        let borrowLimit = 0
        let borrowLimitFormatted, borrowLimitFiat
        if (savingsBalance) {
            borrowLimit = collateralStatus ? collateralFactor * savingsBalance : 0
        }
        borrowLimit = CoreData.toFixed(borrowLimit)
        if (CoreData.isNativeToken(symbol, networkType)) {
          borrowLimitFormatted = CoreData.fromWei(web3, borrowLimit, 'ether')
          borrowLimitFiat = borrowLimitFormatted * price
        } else {
          const decimals = await FetchData.getDecimals(web3, networkType, market);
          borrowLimitFormatted = (borrowLimit / Math.pow(10, parseInt(decimals)))
          borrowLimitFiat = borrowLimitFormatted * price
        }
    
        totalLoanBalance += loanBalanceFiat
        totalBorrowLimitFiat+= borrowLimitFiat
  
        
        results[account][symbol] = {
            loanBalance,
            loanBalanceFormatted,
            loanBalanceFiat,
      
            savingsBalance,
            savingsBalanceFormatted,
            savingsBalanceFiat,
            savingsCTokenBalance,
      
            borrowLimit,
            borrowLimitFormatted,
            borrowLimitFiat,
      
            collateralFactor,
            collateralStatus
        }
      }
      results[account].totalLoanBalance = totalLoanBalance
      results[account].totalBorrowLimitFiat = totalBorrowLimitFiat
      results[account].percent = CoreData.fixedNaN((totalLoanBalance / totalBorrowLimitFiat) * 100)
    }
    return results;
  }

  async function getFilterData(data) {
    let results = []
    const multiData = await getMultiData(data) // multi获取初始数据
    const cData = getCateDataFromMulti(multiData) // 按account、symbol分类
    const assetList = await getAssetData(cData) // 获取贷款使用率，百分比

    data.forEach((item) => {
      const {account} = item
      if (item.health * 100 >= liquidatePercent && assetList && assetList[account] && assetList[account].percent >= liquidatePercent) {
        item.health = Math.max(item.health * 100, assetList[account].percent)
        results.push(item)
      } 
    })

    return results
  }

  // end multi获取数据

  function getTableItemData(marketData, data) {
    let resultJson = {}
    let result = []
    for (const key in data) {
      const value = data[key]
      if (key.split('_').length > 1) {
        const originSymbol = key.split('_')[0] // 原始symbol
        let symbol
        if (Config.markets[originSymbol.toUpperCase()]) {
          symbol = Config.markets[originSymbol.toUpperCase()].symbol
        } else {
          const mappingList = Config.liquidateMarkets[networkType] // 映射表
          symbol = mappingList[originSymbol] // 映射后的symbol
        }

        if (symbol && !resultJson[symbol]) {
          const filterItem = marketData.filter(item => item.symbol === symbol)
          if (filterItem && filterItem.length) {
            resultJson[symbol] = cloneDeep(filterItem[0])
          } 
        }

        if (symbol && value > 0 && /_supply$/g.test(key)) {
          resultJson[symbol].liquidateSupply = value
          resultJson[symbol].liquidateSupplyFiatSort = value * (resultJson[symbol].price || 1)
        }
  
        if (symbol && value > 0 && /_borrow$/g.test(key)) {
          resultJson[symbol].liquidateBorrow = value
          resultJson[symbol].liquidateBorrowFiat = value * (resultJson[symbol].price || 1)
          // 借款资产&钱包余额取小值, 美元高的排前面
          const min = resultJson[symbol].walletBalanceFormatted ? Math.min(value, Number(resultJson[symbol].walletBalanceFormatted)) : value
          resultJson[symbol].liquidateBorrowFiatSort = min * (resultJson[symbol].price || 1)
        }
      }
    }

    for (const key in resultJson) {
      result.push(resultJson[key])
    }

    const myBorrowList = result.sort((a, b) => b['liquidateBorrowFiatSort'] - a['liquidateBorrowFiatSort'])
    const mySupplyList = result.sort((a, b) => b['liquidateSupplyFiatSort'] - a['liquidateSupplyFiatSort'])

    return {
      myBorrowList,
      mySupplyList
    }
  }

  function getTableData(marketData, liquidityData) {
    let tableData = []
    liquidityData.forEach((i, index) => {
        const data = {}
        const {account, total_supply, total_borrow, total_collateral, timestamp, health, ...itemData} = i
        data.account = account
        data.total_supply = +BigNumber(total_supply).times(marketData.usdtInUSD)
        data.total_borrow = +BigNumber(total_borrow).times(marketData.usdtInUSD)
        data.total_collateral = total_collateral
        data.timestamp = timestamp
        data.health = health
        const allList = getTableItemData(marketData, itemData)
        data.myBorrowList = allList.myBorrowList
        data.mySupplyList = allList.mySupplyList
        tableData.push(data)
    })
    return tableData
  }

  useEffect(() => {
    let isUnMounted = false
    async function initialLoad() {
      let fetching = false
      async function loadData() {
        if (!fetching && networkType && networkType !== "unsupported" && readonlyWeb3) {
          try {
            fetching = true
            // console.time()
            let [marketData, liquidityData] = await Promise.all([
              loadAllMarketData(readonlyWeb3, networkType, connectedAddress, marketsArr, hecoMainNetWeb3), 
              loadLiquidityData(networkType)
            ])
            const data = getTableData(marketData, liquidityData)
            const filterData = await getFilterData(data)
            // console.log('filterData------>>>', filterData)
            if (!isUnMounted) {
              setOriginTableData(filterData)
              setTotalPages(Math.ceil(filterData.length / pageSize))
              if (!filterData.length > 0) {
                setLoading(false)
              }
            }
            // console.timeEnd()
          } catch (e) {
            console.error('Error to load markets data:', e)
          } finally {
            fetching = false
          }
        }
      }

      loadData()
    }

    if (networkType === "unsupported") {
      setLoading(false)
      setOriginTableData([])
      setTotalPages(0)
    } else {
      initialLoad()
    }


    return () => {
      isUnMounted = true
    }
  }, [props.data])


  useEffect(() => {
    if (originTableData && originTableData.length > 0) {
      const sortData = sortField === '' ? originTableData : sortTable(originTableData, sortField, sortDirection)
      setTableData(sortData)
      setCurTableData(sortData.slice(pageSize * (currentPage -1), pageSize * currentPage))
      setLoading(false)
    } 
  }, [originTableData, sortField, sortDirection, currentPage])

  const handleClickLiquidate = (e, item, index) => {
    e.stopPropagation()
    setShowLiquidateModal(true)
    setActiveData(item)
    setActiveTableIndex(index)
  }

  const handleExpand = (index) => {
    const newExpand = {...expand, [index]: !expand[index]}
    setExpand(newExpand)
  }

  const handleAssetItem = (e, type, tableIndex, assetIndex) => {
    e.stopPropagation()
    const newSelected = {...assetSelected}
    if (newSelected[tableIndex]) {
      if (newSelected[tableIndex][type] === assetIndex) {
        delete newSelected[tableIndex][type]
      } else {
        newSelected[tableIndex][type] = assetIndex  
      }
    } else {
      newSelected[tableIndex] = {}
      newSelected[tableIndex][type] = assetIndex
    }
    setAssetSelected(newSelected)
  }

  const handleChangePage = (page) => {
    setCurrentPage(page)
    const curTableData = tableData.slice(pageSize * (page - 1), pageSize * page)
    setCurTableData(curTableData)
    setExpand({})
    setAssetSelected({})
  }

  const sortTable = (tableData, field, direction) => {
    return [...tableData].sort((a, b) => {
      return direction ? b[field] - a[field] : a[field] - b[field]
    })
  }

  const handleSortChange = (e, field) => {
    if (e) {
      e.stopPropagation()
    }

    let newField;
    let newDirection
    if (field !== sortField) {
      // 从大到小
      newField = field
      newDirection = true
    } else if (sortDirection) {
      // 从小到大
      newField = sortField
      newDirection = false
    } else {
      // 恢复初始
      newField = ""
      newDirection = true
    }

    const newTableData = newField === '' ? originTableData: sortTable(tableData, newField, newDirection)
    const curTableData = newTableData.slice(pageSize * (currentPage -1), pageSize * currentPage)
    setTableData(newTableData)
    setCurTableData(curTableData)

    setSortField(newField)
    setSortDirection(newDirection)

    setExpand({})
    setAssetSelected({})
  }

  
  // UI Rending
  const renderLoading = (
    <div className={styles.loadingWrap}>
      <ContentLoader
        height={120}
        width={"100%"}
        speed={1}
        backgroundColor={loadingColors.background}
        foregroundColor={loadingColors.foreground}
      >
        <rect x="2%" y="0%" rx="4" ry="4" width="96%" height="18%" />
        <rect x="2%" y="40%" rx="4" ry="4" width="96%" height="18%" />
        <rect x="2%" y="80%" rx="4" ry="4" width="96%" height="18%" />
      </ContentLoader>
    </div>
  );

  const renderAssetItem = (type, item, tableIndex, assetIndex) => {
    const isBorrow = type === 'borrow'
    const value = isBorrow ? item.liquidateBorrow : item.liquidateSupply
    let assetItemSelected = false
    if (assetSelected && assetSelected[tableIndex] && typeof assetSelected[tableIndex][type] === 'number') {
      assetItemSelected =  assetSelected[tableIndex][type] === assetIndex
    }

    return  value > 0 && (
        <div className={classNames(styles.assetItem, {[styles.assetItemSelected]: assetItemSelected})} key={assetIndex} onClick={(e) => handleAssetItem(e, type, tableIndex, assetIndex)}>
          <div>
            {
              !item.isLPToken ?
              <img src={item.logo} width="24" height="24" alt={`${item.symbol} logo`}/> : 
              <TwinLogo
                mode={2}
                logo1={item.logo1}
                logo2={item.logo2} 
              />
            }
            <span>{item.symbol}</span>
          </div>
   
          <div className={styles.txtWrap}>
            <dl>
              <dt>{t('liquidate.Assets')}</dt>
              <dd>{formatDecimalPlace(value, 6)} {item.uiSymbol || item.symbol}</dd>
            </dl>
            <dl>
              <dt>{t('liquidate.Amount')}</dt>
              <dd>${formatDecimalPlace(value * item.price, 6)}</dd>
            </dl>
            <dl className={styles.balance}>
              <dt>{t('liquidate.Balance')}</dt>
              <dd>{formatDecimalPlace(Number(item.walletBalanceFormatted), 6)} {item.uiSymbol || item.symbol}</dd>
            </dl>
          </div>
        </div>
    )
  }

  const renderAssetList = (type, data, tableIndex) => {
    const listKey = type === 'borrow' ? 'myBorrowList' : 'mySupplyList'
    return (
      <div className={classNames(styles.assetList, {[styles.assetListHide]: !expand[tableIndex]})}>
        {
          data[listKey].map((item, assetIndex) => renderAssetItem(type, item, tableIndex, assetIndex))
        }
      </div>
    )
  }

  const renderLiquidateBtn = (item, index) => {
    const isBorrowSelected = assetSelected?.[index]?.borrow >= 0 || false
    let isBorrowWalletZero = false
    if (isBorrowSelected && assetSelected?.[index]?.borrow >= 0) {
      isBorrowWalletZero = Number(item.myBorrowList[assetSelected[index].borrow].walletBalanceFormatted) <= 0
    }
    const isSupplySelected = assetSelected?.[index]?.supply >= 0 || false

    const isSevere = BigNumber(item.health).gte(serverePercent)

    const disabled = !expand[index] || !isBorrowSelected || !isSupplySelected 
                     || !isSevere 
                     || isBorrowWalletZero

    return (
      <Button variant='outline-green' size="lg"  disabled={disabled} onClick={ e => handleClickLiquidate(e, item, index)}>
        {t('Liquidate.liquidation')}
      </Button>
    )
  }


  const renderTableData = () => {
    const table = curTableData.map((item, index) => {
      const isNormal = BigNumber(item.health).lt(warningPercent)
      const isWarning = BigNumber(item.health).gte(warningPercent) && BigNumber(item.health).lt(serverePercent)
      const isSevere = BigNumber(item.health).gte(serverePercent)

      return (
        <div className={styles.poolItemWrap} key={item.account}>
          <Row className={classNames(styles.poolItem, {
            [styles.poolItemDisabledExpand]: item.myBorrowList.length <= 0
          })}  
            onClick={(e) => handleExpand(index)}
          >
            <Col md="auto" className={classNames(styles.value, styles.colExpand)}>
              <div className={classNames(styles.valueContent)}>
                {
                  item.myBorrowList.length > 0 
                    ? (!expand[index]) ? <MdExpandMore/> : <MdExpandLess/>
                    : ''
                }
              </div>
            </Col>
            <Col md="auto" className={classNames(styles.value, styles.colAccount)}>
              <div className={styles.valueHd}>{CoreData.toUpperCase(i18n.language, t('Liquidate.Borrower'))}</div>
              <div className={classNames(styles.valueContent)}>
                {truncateMiddle(item.account, 6, 4, '...')}
              </div>
            </Col>
            <Col md="auto" className={classNames(styles.value, styles.colAssetValue, styles.colBorrowAsset)}>
              <span className={classNames(styles.titleTxt, styles.valueHd, {
                [styles.up]: sortField === 'total_borrow' && !sortDirection,
                [styles.down]: sortField === 'total_borrow' && sortDirection
              })} onClick={(e) => handleSortChange(e, 'total_borrow')}>
                {CoreData.toUpperCase(i18n.language, t('Liquidate.TotalBorrowed'))}
              </span>
              
              <div className={classNames(styles.valueContentWrap)}>
                <div className={classNames(styles.valueContent)}>
                  <span className={styles.valueContentNum}>${formatDecimalPlace(item.total_borrow, 6)}</span>
                </div>
              </div>
            </Col>
            <Col md="auto" className={classNames(styles.value, styles.colArrow)}>
            </Col>
            <Col md="auto" className={classNames(styles.value, styles.colAssetValue, styles.colSupplyAsset)}>
            <span className={classNames(styles.titleTxt, styles.valueHd, {
                [styles.up]: sortField === 'total_supply' && !sortDirection,
                [styles.down]: sortField === 'total_supply' && sortDirection
              })} onClick={(e) => handleSortChange(e, 'total_supply')}>
                {CoreData.toUpperCase(i18n.language, t('Liquidate.Collateral'))}
              </span>
              <div className={classNames(styles.valueContentWrap)}>
                <div className={classNames(styles.valueContent)}>
                  <span className={styles.valueContentNum}>${formatDecimalPlace(item.total_supply, 6)}</span>
                </div>
              </div>
            </Col>
            <Col md="auto" className={classNames(styles.value, styles.colHealth)}>
              <span className={classNames(styles.titleTxt, styles.valueHd,{
                [styles.up]: sortField === 'health' && !sortDirection,
                [styles.down]: sortField === 'health' && sortDirection
              })} onClick={(e) => handleSortChange(e, 'health')}>
                {CoreData.toUpperCase(i18n.language, t('LiquidateModal.health'))}
              </span>
              
              <div className={classNames(styles.valueContent)}>
                {
                  isSevere  ? <span className={classNames(styles.status, styles.severe)}>{t('Liquidate.Severe')} {formatDecimalPlace(item.health, 4) + '%'}</span> : 
                    isWarning ? <span className={classNames(styles.status, styles.warning)}>{t('Liquidate.Warning')} {formatDecimalPlace(item.health, 4) + '%'}</span> : 
                      isNormal && <span className={styles.status}>{t('Liquidate.Normal')} {formatDecimalPlace(item.health, 4) + '%'}</span>
                }
              </div>
            </Col>
            <Col md="auto" className={classNames(styles.value, styles.colOperate)}>
              {renderLiquidateBtn(item, index)}
            </Col>
          </Row>
          {/* 展开后的item */}
          {
            expand[index] && (
              <Row className={styles.poolAsset}>
                <Col md="auto" className={classNames(styles.value, styles.colExpand)}></Col>
                <Col md="auto" className={classNames(styles.value, styles.colAccount)}></Col>
                <Col md="auto" className={classNames(styles.value, styles.colAssetValue, styles.colBorrowAsset)}>
                  <div className={styles.actionStep}>{t('liquidate.ActionStep1')}</div>
                  {renderAssetList('borrow', item, index)}
                </Col>
                <Col md="auto" className={classNames(styles.value, styles.colArrow)}>
                  <div className={classNames(classNames(styles.valueContent, styles.assetArrow))}>
                    <MdExpandMore/>
                  </div>
                </Col>
                <Col md="auto" className={classNames(styles.value, styles.colAssetValue, styles.colSupplyAsset)}>
                  <div className={styles.actionStep}>
                    {t('liquidate.ActionStep2')}
                  </div>
                  {renderAssetList('supply', item, index)}
                </Col>
                <Col md="auto" className={classNames(styles.value, styles.colHealthAndOperate)}>
                  <div className={styles.actionStep}>
                    {t('liquidate.ActionStep3')}
                  </div>
                  <div className={styles.actionBtn}>
                    {renderLiquidateBtn(item, index)}
                  </div>
                </Col>
              </Row>
            )
          }
        </div>
      )
    })

    const pagination = (
      <div className={styles.paginationContainer}>
        <PaginationComponent totalPages={totalPages} currentPage={currentPage} onChangePage={(page) => handleChangePage(page)} />
      </div>
    )
    return originTableData.length > 0  ? (
      <>
        <div className={styles.poolListWrap}>
          {table}
        </div>
        {pagination}
      </>

    ) : <div className={styles.noData}>{t('Common.NoData')}</div>
  }

  const liquidity = (
    <Col className={styles.poolContainer}>
      <div className={styles.infoContainer}>
        <Row className={styles.header}>
          <Col md="auto" className={styles.colExpand}></Col>
          <Col md="auto" className={classNames(styles.colAccount)}>
            <span>
              {t('Liquidate.Borrower')}
            </span>
          </Col>
          <Col md="auto" className={classNames(styles.colAssetValue)} onClick={(e) => handleSortChange(e, 'total_borrow')}>
            <span className={classNames(styles.titleTxt, {
              [styles.up]: sortField === 'total_borrow' && !sortDirection,
              [styles.down]: sortField === 'total_borrow' && sortDirection
            })}>
              {t('Liquidate.TotalBorrowed')}
            </span>
          </Col>
          <Col md="auto" className={styles.colArrow}></Col>
          <Col md="auto" className={classNames(styles.colAssetValue)} onClick={(e) => handleSortChange(e, 'total_supply')}>
            <span className={classNames(styles.titleTxt, {
              [styles.up]: sortField === 'total_supply' && !sortDirection,
              [styles.down]: sortField === 'total_supply' && sortDirection
            })}>
              {t('Liquidate.Collateral')}
            </span>
          </Col>
          <Col md="auto" className={classNames(styles.colHealth)} onClick={(e) => handleSortChange(e, 'health')}>
            <span className={classNames(styles.titleTxt, {
              [styles.up]: sortField === 'health' && !sortDirection,
              [styles.down]: sortField === 'health' && sortDirection
            })}>
              <TorTooltip
                type="line"
                title={t('liquidate.RiskLevel')}
                desc={(
                  <div>
                    <p>{t('liquidate.RiskLevel-tips1')}</p>
                    <p style={{marginTop: 20}}>{t('liquidate.RiskLevel-tips2')}</p>
                  </div>
                )}
              />
            </span>
          </Col>
          <Col md="auto" className={classNames(styles.colOperate)}>
            {t('liquidate.Action')}
          </Col>
        </Row>
        {loading ? renderLoading : renderTableData()}
      </div>
    </Col>
  )

  return (
    <div className={styles.liquidate}>
      <div className={styles.intro}>
        <Container className={styles.introContainer}>
          <h2 className={styles.introTitle}>{t('Liquidate.liquidation')}</h2>
          <p className={styles.introTips}>{t('Liquidate.liquidationTips')}</p>
        </Container>
      </div>
      <div className={styles.poolsContainer}>
        <Container>
          <Row>{liquidity}</Row>
        </Container>
      </div>
      { showLiquidateModal && 
        <LiquidateModal
          data={activeData}
          borrowIndex={assetSelected[activeTableIndex].borrow}
          supplyIndex={assetSelected[activeTableIndex].supply}
          show={showLiquidateModal}
          handleClose={() => setShowLiquidateModal(false)}
          styles={styles}
        />
      }
    </div>
  )
}

export default Liquidate
