import { BigNumber } from 'bignumber.js'
import CoreData from './CoreData'
import Config from '../utils/config'

const gasLimit = 1000000

const approveERC20 = async(web3, connectedAddress, networkType, market) => {

    const contractABI = market.ABI;
    const contractAddress = market.network[networkType].address;
    let contract = await new web3.eth.Contract(contractABI, contractAddress);

    //erc20 qToken Info (eg: qDAI)
    const qContractAddress = market.qToken.network[networkType].address;
    const maxApproval = new BigNumber(2).pow(256).minus(1);

    const gasInfo = await CoreData.getGasInfo(web3);

    const max = maxApproval.toString(10);
    let params = { from: connectedAddress };
    params = await CoreData.recomputeGas(contract.methods.approve(qContractAddress, max), params, gasInfo, networkType);
    let response = await contract.methods.approve(qContractAddress, max).send(params);

    return response
}

