const approveLiquidateERC20 = async(web3, connectedAddress, networkType, market, version ='v1') => {

    const contractABI = market.ABI;
    const contractAddress = market.network[networkType].address;
    let contract = await new web3.eth.Contract(contractABI, contractAddress);

    const maxApproval = new BigNumber(2).pow(256).minus(1);

    const gasInfo = await CoreData.getGasInfo(web3)

    const approveAddress = version === 'v1' ? Config.LiquidateContract[networkType] : market.qToken.network[networkType].address;

    const max = maxApproval.toString(10);
    let params = { from: connectedAddress };
    params = await CoreData.recomputeGas(contract.methods.approve(approveAddress, max), params, gasInfo, networkType);
    let response = await contract.methods.approve(approveAddress, max).send(params);

    return response
}

export default {
    approveERC20,
    approveSwapRepayERC20,
    approveLiquidateERC20,
    approveDepositRepayERC20,
    approveDepositSwapERC20
}
