import React, { useState, useEffect, useContext, useRef } from 'react'
import { Button, Modal, Row, Col, Container } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import classNames from 'classnames'
import { BigNumber } from 'bignumber.js'
import { NetworkTypeContext, WalletAddressContext, Web3Context, DataContext } from '../../../context'
import CoreMethod from '../../../methods/CoreMethod'
import CoreData from '../../../methods/CoreData'
import Config from '../../../utils/config'
import { formatDecimalPlace } from '../../../utils/numberFormat'
import { ModalHeader, ModalPending, ModalSucAndFail, ModalApprove, ModalInput, ModalTips, ModalNum, MoreToolTips } from '../../../components/littleComponents'

export default function LiquidateModal(props) {
    const { connectedAddress } = useContext(WalletAddressContext)
    const { networkType } = useContext(NetworkTypeContext)
    const { web3, version } = useContext(Web3Context)
    const { styles } = props;
    const { t, i18n } = useTranslation();

    const [borrowAsset, setBorrowAsset] = useState()
    const [borrowValue, setBorrowValue] = useState('')
    const [maxBorrow, setMaxBorrow] = useState(0)

    const [supplyAsset, setSupplyAsset] = useState()
    const [supplyValue, setSupplyValue] = useState('')
    const [maxSupply, setMaxSupply] = useState(0)

    const [loading, setLoading] = useState(false)
    const [repayCompleted, setRepayCompleted] = useState(false)
    const [repayFailed, setRepayFailed] = useState(false)
    const [txnHash, setTxnHash] = useState(null)

    const [overPay, setOverPay] = useState(false)
    const [lowBalance, setLowBalance] = useState(false)
    const [lowUserSupply, setLowUserSupply] = useState(false)

    const [firstApproval, setFirstApproval] = useState(true)
    const isUnMountedRef = useRef(false)

    const liquidateIncentive = 1.1
    const discount = 91
    const borrowLimitPercent = 0.5 // 还款额不能超过最大数量的50%


    useEffect(() => {
      const borrowAsset = props.data.myBorrowList[props.borrowIndex]
      setBorrowAsset(borrowAsset)
      setMaxBorrow(borrowAsset ? borrowAsset.liquidateBorrow * borrowLimitPercent : 0)

      const supplyAsset = props.data.mySupplyList[props.supplyIndex]
      setSupplyAsset(supplyAsset)
      setMaxSupply(supplyAsset ? supplyAsset.liquidateSupply : 0)
    }, [])

    const calcBonus = () => {
      if (!borrowAsset || !supplyAsset) return 0
      const borrowPrice = borrowAsset.price * borrowValue
      const supplyPrice = supplyAsset.price * supplyValue
      return '$' + formatDecimalPlace(supplyPrice - borrowPrice, 2)
    }

    const validateInputs = async(borrowValue, supplyValue) => {
      if (new BigNumber(borrowValue).isNaN()) borrowValue = '0'
      if (new BigNumber(supplyValue).isNaN()) supplyValue = '0'

      setLowBalance(new BigNumber(borrowAsset.walletBalanceFormatted).isLessThan(borrowValue)) // 钱包余额不足
      setOverPay(new BigNumber(maxBorrow).isLessThan(borrowValue)) // 还款额超过最大数量的50%
      setLowUserSupply(new BigNumber(maxSupply).isLessThan(supplyValue)) // 用户供应不足
    }

    const resetAlerts = () => {
        setOverPay(false)
        setLowBalance(false)
        setLowUserSupply(false)
    }

    const resetInputs = () => {
        resetAlerts()
        setBorrowValue('')
        setSupplyValue('')
    }

    const handleClose = (closeModal = true) => {
      resetAlerts()
      setBorrowAsset()
      setBorrowValue('')
      setMaxBorrow(0)

      setSupplyAsset()
      setSupplyValue('')
      setMaxSupply(0)

      setLoading(false)
      setRepayCompleted(false)
      setRepayFailed(false)
      setTxnHash(null)
      setFirstApproval(true)
      if (closeModal) {
        props.handleClose()
        isUnMountedRef.current = true
      }
    }

    // 点击借款max || 用户输入
    const handleChangeBorrow = (value, isMax=false) => {
      resetAlerts()
      setBorrowValue(value.toString())
      calculateSupply(isMax, value.toString())
    }

    const calculateSupply = async (isMax, borrowValue) => {
      if (new BigNumber(borrowValue).isZero() || new BigNumber(borrowValue).isNaN()) {
          resetAlerts()
          setSupplyValue('')
          return
      }
      const supplyValue = borrowValue * borrowAsset.price / supplyAsset.price * liquidateIncentive
      setSupplyValue(supplyValue.toString())
      validateInputs(borrowValue, supplyValue)
    }

    // 点击抵押max
    const handleSupplyMax = () => {
      resetAlerts()
      setSupplyValue(maxSupply.toString())
      calculateBorrow(maxSupply.toString())
    }

    const calculateBorrow = async (supplyValue) => {
      if (new BigNumber(supplyValue).isZero() || new BigNumber(supplyValue).isNaN()) {
          resetAlerts()
          setBorrowValue('')
          return
      }
      const borrowValue = supplyValue * supplyAsset.price / borrowAsset.price / liquidateIncentive
      setBorrowValue(borrowValue.toString())
      validateInputs(borrowValue, supplyValue)
    }

  const handleApprove = async (first = true) => {
    setLoading(true)
    await CoreMethod.approveLiquidateERC20(web3, connectedAddress, networkType, borrowAsset, version)
        .then(async response => {
            if (response && first) {
              setFirstApproval(false) 
            }
            setLoading(false)
        })
        .catch(error => {
            if (error.code === 4001) {
                handleClose(false)
            }
        })
  }

  const getExrate = async () => {
    const ctoken = await CoreData.getQTokenContract(web3, networkType, supplyAsset.symbol)
    const exrate = await ctoken.methods.exchangeRateCurrent().call();
    return exrate;
  }

  const handleLiquidate = async (handleUpdate) => {
    const gtagParams = {
      url: window.location.href,
      LiquidationSymbol: borrowAsset.symbol,
      CollateralSymbol: supplyAsset.symbol
    }

    setFirstApproval(true)
    setLoading(true)
    const qcontract = await CoreData.getQTokenContract(web3, networkType, borrowAsset.symbol)
    const borrowerAddress = props.data.account // 借款人
    const borrowAmount = await CoreData.getRawValue(web3, networkType, borrowAsset.symbol, borrowValue) // 清算数量(借款)
    const supplyQtokenAddress = Config.markets[supplyAsset.symbol].qToken.network[networkType].address // 抵押物的qtoken地址
    const supplyAmount = await CoreData.getRawValue(web3, networkType, supplyAsset.symbol, supplyValue) // 抵押数量(存款)

    // console.log('借款-->', borrowValue, borrowAmount, borrowAsset.price)
    // console.log('抵押-->', supplyValue, supplyAmount, supplyAsset.price)
    // console.log('抵押exrate-->', await getExrate())

    try {
        let response
        if (CoreData.isNativeToken(borrowAsset.symbol, networkType)) {
          let gasEstimate = await qcontract.methods.liquidateBorrow(borrowerAddress, supplyQtokenAddress).estimateGas({
            from: connectedAddress,
            value: borrowAmount
          })
          response = await qcontract.methods.liquidateBorrow(borrowerAddress, supplyQtokenAddress).send({
            from: connectedAddress,
            value: borrowAmount,
            gasLimit: gasEstimate * 2
          }).on('transactionHash', (hash) => {
            if (!isUnMountedRef.current) {
              setTxnHash(hash)
            }
          })
        } else {
          let gasEstimate = await qcontract.methods.liquidateBorrow(borrowerAddress, borrowAmount, supplyQtokenAddress).estimateGas({
            from: connectedAddress
          })
          response = await qcontract.methods.liquidateBorrow(borrowerAddress, borrowAmount, supplyQtokenAddress).send({
            from: connectedAddress,
            gasLimit: gasEstimate * 2
          }).on('transactionHash', (hash) => {
            if (!isUnMountedRef.current) {
              setTxnHash(hash)
            }
          })
        }

        if (response.events.Failure) {
          window.gtag('event', 'liquidation', {
            ...gtagParams,
            error: 'response event fail'
          })
          if (!isUnMountedRef.current) {
            setRepayFailed(true)
            setLoading(false)
          }
        } else {
          window.gtag('event', 'liquidation', gtagParams)
          if (!isUnMountedRef.current) {
            handleUpdate()
            setRepayCompleted(true)
            setLoading(false)
          }
        }
    } catch (error) {
        console.log(error)
        if (error.code === 4001) {
            window.gtag('event', 'liquidation', {
              ...gtagParams,
              error: 'user rejected'
            })
            if (!isUnMountedRef.current) {
              handleClose(false)
            }
        } else {
            window.gtag('event', 'liquidation', {
              ...gtagParams,
              error: 'other error'
            })
            if (!isUnMountedRef.current) {
              setRepayFailed(true)
              setLoading(false)
            }
        }
    } finally {
      if (!isUnMountedRef.current) {
        setTxnHash(null)
      }
    }
  }

  // UI rending

  const FooterList = () => {
    const walletNum = +BigNumber(borrowAsset.walletBalanceFormatted)
    return (
      <div className="footerList">
        <div className="footerItem">
            <div className="label">{t('LiquidateModal.WalletBalance')}</div>
            <div className="value">
                {`${walletNum === 0 ? 0 : formatDecimalPlace(walletNum, 4)} ${borrowAsset.uiSymbol || borrowAsset.symbol}`}
            </div>
        </div>
      </div>
    )
  }


  const ModalLoading = 
    <ModalSucAndFail 
        type="loading"
        txnHash={txnHash}
        title={t('CommonModal.PleaseWait')}
    />

  const TxnSuccessMsg =
    <ModalSucAndFail 
        type="success"
        txnHash={txnHash}
        content={t('LiquidateModal.SuccessMsg')}
        okText='OK'
        handleOk={handleClose}
        footerList={borrowAsset && FooterList()}
    />

  const TxnErrorMsg =
    <DataContext.Consumer>
        {
            ({handleUpdate}) => (
                <ModalSucAndFail 
                    type="error"
                    txnHash={txnHash}
                    title={t('LiquidateModal.ErrorMsg')}
                    okText={t('CommonModal.Retry')}
                    handleOk={() => handleLiquidate(handleUpdate)}
                    footerList={borrowAsset && FooterList()}
                />
            )
        }
    </DataContext.Consumer>

  const ModalApprovalRequest = 
  <ModalSucAndFail 
    type="approve"
    showTitle={false}
    showTransation={false}
    content={t('CommonModal.ApprovalMsg', {token: CoreData.getApprovalMsgToken(networkType)})}
    okText={t('CommonModal.Approve')}
    handleOk={handleApprove}
  />


  const ModalRepayForm = () => {
    return (
      <div className="contentContainer">
         <div className="formContainer">
            {
              borrowAsset && (
                <div className={styles.formInputWrap}>
                  <div className={styles.formInfo}>
                    <span className={styles.formInfoHd}>
                      {t('LiquidateModal.LiquidationAmount')}
                    </span>
                  </div>
                  <ModalInput
                    controlId="formBorrow"
                    data={borrowAsset}
                    inputValue={borrowValue}
                    onChange={e => handleChangeBorrow(e.target.value, false)}
                    showMax={borrowAsset}
                    maxOnClick={() => handleChangeBorrow(maxBorrow, true)}
                    showImg={true}
                  />
                  <div className={styles.formInfo}>
                    <span className={styles.formInfoHd}>
                      { 
                        `${t('LiquidateModal.Maximum')}: ${maxBorrow === 0 ? 0 : formatDecimalPlace(maxBorrow, 6)} ${borrowAsset.uiSymbol || borrowAsset.symbol}` 
                      }
                    </span>
                  </div>
                </div>
              )
            }

            {
              supplyAsset && (
                <div className={styles.formInputWrap}>
                  <div className={styles.formInfo}>
                    <span className={styles.formInfoHd}>
                      {t('LiquidateModal.CollateralReceived')}
                    </span>
                  </div>
                  <ModalInput
                    controlId="formSupply"
                    data={supplyAsset}
                    inputValue={supplyValue}
                    showMax={true}
                    maxOnClick={handleSupplyMax}
                    disabled={true}
                    showImg={true}
                  />
                  <div className={styles.formInfo}>
                    <span className={styles.formInfoHd}>
                      { 
                        `${t('LiquidateModal.Maximum')}: ${maxSupply === 0 ? 0 : formatDecimalPlace(maxSupply, 6)} ${supplyAsset.uiSymbol || supplyAsset.symbol}`
                      }
                    </span>
                  </div>
                </div>
              )
            }

          {
              lowBalance ? (
                <ModalTips 
                  smallIconTips={true}
                  showTitle={false}
                  type={'errorTips'} 
                  content={t('LiquidateModal.LowBalanceError')} // 钱包余额不足
                />
              ) : overPay ? (
                <ModalTips 
                  smallIconTips={true}
                  showTitle={false}
                  type={'errorTips'} 
                  content={t('LiquidateModal.OverPayError')} // 还款额超过最大数量
                /> )
              : lowUserSupply ? (
                <ModalTips 
                    smallIconTips={true}
                    showTitle={false}
                    type={'errorTips'} 
                    content={t('LiquidateModal.LowUserSupply')} // 用户供应不足
                  /> )
              : ''
            }

            <div className={styles.infoList}>
              <div className={styles.infoItem}>
                <div className={styles.value}>
                  {borrowAsset ? '$' + formatDecimalPlace(borrowValue * borrowAsset.price, 2) : '$0'}
                </div>
                <div className={styles.label}>{t('LiquidateModal.LiquidationAmount')}</div>
              </div>
              <div className={styles.infoItem}>
                  <div className={styles.value}>
                    {supplyAsset ? '$' + formatDecimalPlace(supplyValue * supplyAsset.price, 2) : '$0'}
                  </div>
                  <div className={styles.label}>{t('LiquidateModal.CollateralReceived')}</div>
              </div>
              <div className={styles.infoItem}>
                <div className={styles.value}>
                  {100 - discount}%
                </div>
                <div className={styles.label}>{t('LiquidateModal.Discount')}</div>
              </div>
              <div className={styles.infoItem}>
                <div className={styles.value}>
                  {calcBonus()}
                </div>
                <div className={styles.label}>{t('LiquidateModal.Reward')}</div>
              </div>
            </div>
         </div>
         <div className="buttonsContainer">
            <DataContext.Consumer>
                {
                    ({handleUpdate}) => (
                        <Button 
                            variant="green" 
                            disabled = {overPay  || lowBalance || lowUserSupply || isNaN(parseFloat(borrowValue)) || isNaN(parseFloat(supplyValue)) || parseFloat(borrowValue) <= 0 || parseFloat(supplyValue) <= 0}
                            onClick={() => handleLiquidate(handleUpdate)}
                        >
                            {t('Liquidate.liquidation')}
                        </Button>
                    )
                }
            </DataContext.Consumer>
         </div>
         {borrowAsset && FooterList()}
      </div>
    )
  }

  const needsApproval = borrowAsset && !borrowAsset.liquidateApproved && firstApproval 
  const ModalRendered = () => needsApproval ? ModalApprovalRequest : ModalRepayForm()

  return (
    <Modal
      show={props.show}
      onHide={handleClose}
      className={classNames('customModal', styles.txnModal)}
      centered
      animation={false}
    >
      <Modal.Header closeButton>
        <ModalHeader
            title={t('LiquidateModal.Heading')}
        />
      </Modal.Header>
      {
          loading ? ModalLoading : 
            repayCompleted ? TxnSuccessMsg :
              repayFailed ? TxnErrorMsg : ModalRendered()
      }
    </Modal>
  )
}
